"use client"; // Marking this file as a client component

import React, { useRef, useEffect, useState } from "react";
import { Button } from "./ui/button"; // Use named import for Button component

const Game: React.FC = () => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const animationRef = useRef<number>();
  const [gameStatus, setGameStatus] = useState<"idle" | "running" | "paused" | "over">("idle");
  const [carX, setCarX] = useState(100); // Initial x position
  const carY = 300; // Fixed y position for simplicity
  const [bugattiImage, setBugattiImage] = useState<HTMLImageElement | null>(null);

  useEffect(() => {
    // Load Bugatti image
    const img = new Image();
    img.src = `https://placehold.co/150x100?text=Stylish+Bugatti+Sports+Car+in+motion`;
    img.onload = () => setBugattiImage(img);
    img.onerror = () => {
      console.error("Failed to load Bugatti image.");
    };
  }, []);

  // Handle keyboard input for car movement
  const handleKeyDown = (e: KeyboardEvent) => {
    if(gameStatus !== "running") return;
    if (e.key === "ArrowLeft") {
      setCarX((prev) => Math.max(prev - 10, 0));
    } else if (e.key === "ArrowRight") {
      setCarX((prev) => Math.min(prev + 10, 500)); // Assume canvas width boundaries
    }
  };

  // Game loop drawing function
  const draw = () => {
    const canvas = canvasRef.current;
    const ctx = canvas?.getContext("2d");
    if (!canvas || !ctx) {
      console.error("Canvas or context not found.");
      return;
    }
    try {
      // Clear canvas
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      // Draw road background (centered road)
      ctx.fillStyle = "#555";
      const roadWidth = canvas.width * 0.6;
      const roadX = (canvas.width - roadWidth) / 2;
      ctx.fillRect(roadX, 0, roadWidth, canvas.height);

      // Draw dashed centerline for road
      ctx.strokeStyle = "#FFF";
      ctx.lineWidth = 5;
      ctx.setLineDash([20, 15]);
      ctx.beginPath();
      ctx.moveTo(canvas.width / 2, 0);
      ctx.lineTo(canvas.width / 2, canvas.height);
      ctx.stroke();

      // Draw Bugatti car if image loaded, else draw a fallback rectangle
      if (bugattiImage) {
        ctx.drawImage(bugattiImage, carX, carY, 150, 100);
      } else {
        ctx.fillStyle = "red";
        ctx.fillRect(carX, carY, 150, 100);
      }
    } catch (error) {
      console.error("Error during game drawing loop:", error);
    }

    // Continue the loop if game is running
    if (gameStatus === "running") {
      animationRef.current = requestAnimationFrame(draw);
    }
  };

  // Start and pause game functions
  const startGame = () => {
    setGameStatus("running");
    animationRef.current = requestAnimationFrame(draw);
  };

  const pauseGame = () => {
    setGameStatus("paused");
    if (animationRef.current) cancelAnimationFrame(animationRef.current);
  };

  const restartGame = () => {
    setCarX(100);
    setGameStatus("idle");
    startGame();
  };

  useEffect(() => {
    window.addEventListener("keydown", handleKeyDown);
    return () => {
      window.removeEventListener("keydown", handleKeyDown);
      if (animationRef.current) cancelAnimationFrame(animationRef.current);
    };
  }, [gameStatus]);

  return (
    <div>
      <canvas
        ref={canvasRef}
        width={800}
        height={600}
        style={{
          border: "2px solid #333",
          backgroundColor: "#222",
          display: "block",
          margin: "0 auto"
        }}
      >
        Seu navegador n√£o suporta canvas.
      </canvas>
      <div style={{ marginTop: "1rem", textAlign: "center" }}>
        {gameStatus === "idle" && (
          <Button variant="default" onClick={startGame}>Iniciar Corrida</Button>
        )}
        {gameStatus === "running" && (
          <Button variant="default" onClick={pauseGame}>Pausar Corrida</Button>
        )}
        {gameStatus !== "idle" && (
          <Button variant="default" onClick={restartGame} style={{ marginLeft: "1rem" }}>
            Reiniciar
          </Button>
        )}
      </div>
    </div>
  );
};

export default Game;
